
































































import { Component, Vue } from 'vue-property-decorator';
import Result from './Result.vue';
import 'codemirror/mode/clike/clike.js'
import 'codemirror/theme/material.css'

@Component({
  components: {
    Result
  },
})

export default class Editor extends Vue {
  namefile = ""
  commandHistory = []
  command = ""
  input = ""
  output: Array<[string, string]> = []

  status = "success"
  show = false
  mode = ""

  evaluatorOptions = {
    tabSize: 2,
    theme: 'material',
    lineWrapping: true
  }
  inputOptions = {
    tabSize: 2,
    mode: 'text/x-java',
    theme: 'material',
    lineNumbers: true,
    lineWrapping: true
  }
  outputOptions = {
    tabSize: 2,
    mode: 'text',
    theme: 'material',
    lineNumbers: false,
    readOnly: true,
    lineWrapping: true
  }

  get disabled() {
    return this.status === "secondary"
  }
  get icon() {
    switch (this.status) {
      case "danger": return "exclamation-circle"
      case "success": return "paper-plane"
      default: return ""
    }
  }

  async compile() {
    try {
      this.status = "secondary"
      const response = await fetch('http://localhost:8077/compile', {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          source: this.input,
          name: this.namefile
        })
      })
      const output = (await response.json()).messages
      this.output = JSON.parse(output)
      this.status = "success"
    } catch (err) {
      this.status = "danger"
      console.error(err)
    }
  }
  async execute() {
    try { 
      /*const response = undefined await fetch('http://localhost:8077/execute', {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          file: this.command.slice(0,-1)
        })
      })
      const output = (await response.json()).messages    
      this.command = JSON.parse(output) */
    } catch (err) {
      console.error(err)
    }
  }
  clearInput() {
    this.mode = "input"
    this.show = true
  }
  clearOutput() {
    this.mode = "output"
    this.show = true
  }
  clear() {
    this.show = false
    setTimeout(() => this.mode = "", 300)
    if (this.mode == "input") this.input = ""
    else this.output = []
  }
  executeCommand() {
    this.command = this.command.slice(0,-3)
    //this.commandHistory.push(this.command) // Something happen . . .?
    if(this.command == "clear") this.commandHistory = []
    else this.execute()
    this.command = ""
  }
}
